.music__player {
    color: #bfbfbf;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 30px;
    box-sizing: border-box;
    height: 50px;
    width: 100%;
    left: 0;
    bottom: 0;
    z-index: 100;
    position: absolute;
    background-color: $blue-bcg;

    @include media-breakpoint-down(lg) {
        padding: 0 18px;
    }   

    @include media-breakpoint-down(md) {
        height: 70px;
    }

    @include media-breakpoint-down(sm) {
        height: 60px;
        width: calc(100% - 36px);
        left: 18px;
        padding: 0px 18px;
        bottom: 74px;
        border-radius: 12px;
    }

    .fas, .far {
        cursor: pointer;
    }

    &-artist {
        height: 76%;
        position: relative;
        left: -23px;
        display: flex;
        justify-content: space-between;

        &-wrap {
            height: 100%;
            width: 53.2px;
            overflow: hidden;

            img {
                width: 100%;
            }
        }
    }
    

    &-controls {
        width: 90px;
        justify-content: space-between;
        display: flex;
        font-size: 13px;
        position: relative;
        align-items: center;

        @include media-breakpoint-down(md) {
            position: absolute;
            left: 50%;
            top: 14px;
            transform: translateX(-50%);
        }
   
        @include media-breakpoint-down(sm) {
            position: relative;
            top: 0;
            left: 0;
            transform: translateX(0);
            z-index: 2;
        }

        .play_btn {
            font-size: 28px;
            color: #2BA6A2;
        }   
        
        #play_prev, #play_next {
            @include media-breakpoint-down(sm) {
                display: none;
            }
        }
    }

    &-song-desk {
        display: none;

        @include media-breakpoint-down(sm) {
            display: flex;
            flex-direction: column;
        }
    }
    

    &-timeline {
        width: 70%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include media-breakpoint-down(md) {
            position: relative;
            top: 20px;
        }

        @include media-breakpoint-down(sm) {
            display: flex;
            align-items: flex-end;
            position: absolute;
            bottom: 0;
            width: calc(100% - 24px);
            left: 0;
        }

        span {
            font-size: 12px;
            text-align: center;
            
            @include media-breakpoint-down(sm) {
                display: none;
            }
        }

        input[type="range"] {
            width: 90%;
            height: 3px;
            display: block;
            margin: 0 auto;
            cursor: pointer;
            background: linear-gradient(90deg, rgb(99, 111, 255) 60%, rgb(255, 255, 255) 60%);
            border-radius: 10px;
            // -webkit-appearance: none;

            // &::-webkit-slider-runnable-track {
            //     width: 40px;
            //     height: 100%;
            //     border-radius: 5px;
            //     -webkit-appearance: none;
            //     position: r;
            //     top: -p;
            //     background-color: rgba(99, 111, 255, 40%);/*#636fff*/
            // }

            //     -webkit-appearance: none;
            //     height: 8px;
            //     width: 8px;
            //     position: relative;
            //     top: -2px;
            //     border-radius: 50%;
            //     background-color: #636fff;
            //     transition: .1s all ease;
            //     cursor: pointer;
            
            //     &:hover {
            //         width: 12px;
            //         height: 12px;
            //         top: -4px;
            //     }
            // }
            
            @include media-breakpoint-down(sm) {
                width: 100%;
                
                // &::-webkit-slider-thumb {
                //     display: none;
                // }
            }
        }  
    }

    &-bar {
        display: flex;
        justify-content: space-between;
        width: 150px;
        align-items: center;

        #repeat_all {
            width: 16px;
        }
    }

    &-volume {
        display: flex;
        position: relative;
        left: 0;
        align-items: center;
        transition: .2s all ease;
        outline: none;
        width: 12%;

        
        &-range {
            display: none;
            width: 50px;
            height: 1.7px;
            border-radius: 5px;
            -webkit-appearance: none;
            margin-left: 5px;
            cursor: pointer;
            background-color: #bfbfbf;
            
            &::-webkit-slider-thumb {
                -webkit-appearance: none;
                height: 9px;
                width: 9px;
                border-radius: 8px;
                border: 2px solid #bfbfbf;
                background-color: #0f1626;
                transition: .2s all;
                cursor: pointer;

                &:hover {
                    background-color: #bfbfbf;
                    transform: scale(1.2);
                }

                &:active {
                    background-color: #bfbfbf;
                }
            }
            
        }

        &:hover {
            left: -25px;
            width: 33%;
        }

        &:hover &-range {
            display: block;
        }
    }
}

